:root {
    // logo color

    --primary-logo-color: rgba(196, 196, 196, 0);
    --secondary-logo-color: rgba(196, 196, 196, 0.03);
    // градиент logo

    --gradient-logo-color: background: linear-gradient(136.07deg, rgba(196, 196, 196, 0.03) 10.75%, rgba(196, 196, 196, 0) 92.33%);


    // background
    //  rgba(11, 11, 11, 0.5); в Header
    --primary-bg-color: rgba(11, 11, 11, 0.5);
    --black-bg-color: #0E0E0E;

    // input //////////////////////////////////////////////

    --primary-input-bg-color: rgba(255, 255, 255, 0.2);
    --primary-input-text-color: rgba(204, 204, 204, 1);

    // modal/////////////////////////////////////////////////

    --primary-modal-bg-color: rgba(255, 242, 253, 1);
    --secondary-modal-bg-color: rgba(32, 13, 29, 0.6);

    // шрифт \\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\

    --primary-blue-text-color: rgba(78, 115, 223, 100);

    --primary-white-text-color: rgba(255, 255, 255, 1);
    --primary-black-text-color: #0E0E0E;

    --pink-text-color: rgba(220, 86, 197, 1);

    --gray-text-color: rgba(147, 147, 147, 1);

    --blue-text-color: rgba(76, 0, 254, 1);

    --black-text-color: rgba(14, 14, 14, 1);

    --primary-text-font: "Montserrat", "sans-serif";
    --secondary-text-font: "Raleway", "sans-serif";

    --title-text-animation-color: #a91079;

    //    color icon ////////////////////////////////////////////
    --icon-gray-bg-color: rgba(147, 147, 147, 1);

    --white-icon-color: rgba(255, 255, 255, 1);

    --gray-icon-color: rgba(147, 147, 147, 1);

    --blue-icon-color: rgba(76, 0, 254, 1);

    --black-icon-color: rgba(14, 14, 14, 1);
    // обводка
    --outline-border-color: rgba(220, 86, 197, 0.3);

}










