.dark h1 {
  position: relative;
  animation: blink1 8s infinite;
  color: var(--black-text-color);
  z-index: 200;
}

.dark .container1 {
  @media screen and (min-width: 319.98px) and (max-width: 767.98px) {
    background-image: url(../images/main-bg/bg-body-phone.png);
    background-repeat: no-repeat;
    width: 400px;
    height: 2750px;
    background-size: 1500px;
    background-position: 81px 51px;
    filter: brightness(300%) contrast(158%) grayscale(38%) hue-rotate(165deg)
      invert(92%) opacity(89%) saturate(131%) sepia(17%);
  }

  // tablet

  @media screen and (min-width: 768px) {
    background-image: url(../images/main-bg/bg-body-tablet.png);
    background-repeat: no-repeat;
    width: 768px;
    height: 1620px;
    background-size: 1500px;
    background-position: center 51px;
    filter: brightness(300%) contrast(158%) grayscale(38%) hue-rotate(165deg)
      invert(92%) opacity(89%) saturate(131%) sepia(17%);
  }

  // deckstop

  @media screen and (min-width: 1280px) {
    background-image: url(../images/main-bg/bg-body-deckstop.png);
    background-repeat: no-repeat;

    width: 1280px;
    height: 2120px;
    background-size: 1500px;
    background-position: center 51px;
    filter: brightness(300%) contrast(158%) grayscale(38%) hue-rotate(165deg)
      invert(92%) opacity(89%) saturate(131%) sepia(17%);

    box-shadow: rgba(240, 46, 170, 0.4) -5px 5px,
      rgba(240, 46, 170, 0.3) -10px 10px, rgba(240, 46, 170, 0.2) -15px 15px,
      rgba(240, 46, 170, 0.1) -20px 20px, rgba(240, 46, 170, 0.05) -25px 25px;
  }
}

.toggle {
  position: fixed;
  top: -75px;
  // left: -75px;
  right: 0;
  width: 150px;
  height: 150px;
  border: none;
  border-radius: 50%;
  z-index: 498;
}

.toggle .toggle-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 150px;
  height: 150px;
  border: none;
  border-radius: 50%;
  z-index: 500;
  cursor: pointer;
  transition: 0.3s linear;
}

.toggle .toggle-overlay:hover {
  box-shadow: 0 0 10px #dc56c5;
}

.toggle .moon,
.toggle .sun {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  z-index: 499;
}

.toggle .sun {
  background: radial-gradient(#f9ca24, #f6e58d);
  box-shadow: 0 0 10px 2px #fbc531;
  position: absolute;
  top: 85px;
  left: 85px;
  box-shadow: 0 0 10px 2px #eeeeee;
  transform: rotate(160deg);
  background: #dc56c5;
}

.toggle .moon {
  background: #dedede;
  box-shadow: 0 0 10px 2px #eeeeee;
  position: absolute;
  top: 25px;
  left: 25px;
  transform: rotate(160deg);
  background: radial-gradient(#f9ca24, #f6e58d);
}

.toggle .sun::before {
  content: '';
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: rgba(32, 32, 64, 0.9);
}

.toggle.day {
  animation: rotateDay 0.45s linear 0s 1 forwards;
}

.toggle.night {
  animation: rotateNight 0.45s linear 0s 1 forwards;
}

.toggle.night .toggle-overlay:hover {
  box-shadow: 0 0 10px;
}

@keyframes rotateNight {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(180deg);
  }
}

@keyframes rotateDay {
  0% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes opacityOn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes opacityOff {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

